import { mapMutations, mapActions } from 'vuex'
const debug = process.env.NODE_ENV !== 'production'

export default {
  computed: {
    link () {
      let devLink = '/static/html/index.html'
      let proLink = '/weixin3.0/static/html/index.html'

      return debug ? devLink : proLink
      // return 'http://wx.sepco1.com/portal/json/security/doPowerNewIndex.action?hum=250feda5-863d-466f-8f25-1af54b18b5be'
    }
  },
  mounted () {
    this.loadPage()
  },
  methods: {
    loadPage () {
      let that = this
      that.showLoading(true)
      try {
        that.$refs.iframe.onload = function () {
          that.showLoading(false)
        }
      } catch (e) {
        that.AlertShowEvent(e.message)
      }
    },
    ...mapMutations({
      showLoading: 'SHOWLOADING'
    }),
    ...mapActions([
      'AlertShowEvent'
    ])
  }
}



