
import Alert from 'vux/src/components/alert/index.vue'


const ERROR = '链接参数错误'

export default {
data () {
return {
alertShow: false,
alertTitle: '提示',
message: ''
}
},
created () {
this.goToPage()
},
methods: {

goToPage () {
let { query } = this.$router.history.current
let { url } = query

if (url) {
this.$router.push(url)
} else {
this.ShowHideEvent(ERROR)
}
},

AlertHideEvent () {
this.alertShow = false
},

ShowHideEvent (msg) {
this.message = msg
this.alertShow = true
}
},
components: {
Alert
}
}



